<template>
    <div >
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <el-alert
                title=""
                type="info"
                center
                :closable="false"
                show-icon>
            </el-alert>
            <!--  -->
            <el-steps :active="active" finish-status="success" align-center>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
            </el-steps>
            <el-form ref="editGoodFormRef" :model="editGoodForm" :rules="editGoodFormRules" label-width="80px" label-position="top">
                <el-tabs tab-position="left" :before-leave="changeTab">
                    <el-tab-pane label="">
                        <el-form-item label="" prop="goods_name">
                            <el-input v-model="editGoodForm.goods_name"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_price">
                            <el-input v-model="editGoodForm.goods_price"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_weight">
                            <el-input v-model="editGoodForm.goods_weight"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_number">
                            <el-input v-model="editGoodForm.goods_number"></el-input>
                        </el-form-item> 
                    </el-tab-pane>
                    <el-tab-pane label="">
                        <div class="manyAttr" :key='item.attr_id' v-for='item in manyAttrs'>
                            <el-row>
                                {{ item.attr_name }}
                            </el-row>
                            <el-row>
                                <el-checkbox @change="choseVal(item.attr_id, val, $event)" checked :key="index" v-for="(val, index) in item.attr_vals" :label="val" border></el-checkbox>
                            </el-row>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="">
                        <el-form-item :key="item.attr_id" v-for="item in onlyAttrs" :label="item.attr_name">
                            <el-input v-model="item.attr_vals" ></el-input>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="">
                        <el-upload
                            class="upload-demo"
                            action=""
                            :file-list="imgList"
                            list-type="picture"
                            :auto-upload="false">
                        <el-button size="small" type="primary"></el-button>
                        </el-upload>
                    </el-tab-pane>
                    <el-tab-pane label="">
                        <quill-editor 
                            v-model="editGoodForm.goods_introduce"
                            class="editor"
                            ref="quillEditor"
                            @blur="onEditorBlur($event)">
                        </quill-editor>
                    </el-tab-pane>
                </el-tabs>
            </el-form>
        </el-card>
    </div>
</template>

<script>
export default {
  name: 'EditGood',
  props: ['id'],
  data () {
      return {
          active: 0,
          editGoodForm: {
              goods_name: '',
              goods_price: '',
              goods_number: '',
              goods_weight: '',
              goods_introduce: '',
              pics: {},
              attrs: [],
              goods_cat: '',//
          },
          editGoodFormRules: {
            goods_name: [
                { required: true, message: '', trigger: 'blur' },
            ],
            goods_price: [
                { required: true, message: '', trigger: 'blur' },
            ],
            goods_weight: [
                { required: true, message: '', trigger: 'blur' },
            ],
            goods_number: [
                { required: true, message: '', trigger: 'blur' },
            ],
          },
          currentClass: '',
          goodClass: [],
          goodClassOptions: [],
        //   
          manyAttrs: [],
        //   
          onlyAttrs: [],
          imgList: [],
      }
  },
  created () {
      this.getGoodClass()
      this.getGood()
  },
  methods: {
    //   
      async getGood () {
        const {data: res} = await this.$http.get(`goods/${this.id}`)
        if(res.meta.status !== 200) return this.$message.error('')
        this.editGoodForm.goods_name = res.data.goods_name
        this.editGoodForm.goods_price = res.data.goods_price
        this.editGoodForm.goods_number = res.data.goods_number
        this.editGoodForm.goods_weight = res.data.goods_weight

        this.editGoodForm.goods_introduce = '<p>' + res.data.goods_introduce + '</p>'

        this.editGoodForm.goods_cat = res.data.goods_cat
        var good_cats = res.data.goods_cat.split(',')
        var good_cat_id = good_cats[good_cats.length-1]
        //   []
        const {data: res_many} = await this.$http.get(`categories/${good_cat_id}/attributes`, { params:  {sel: "many"} })
        if(res_many.meta.status !== 200) return this.$message.error('')
        this.manyAttrs = res_many.data
        this.manyAttrs.forEach(val => {
            val.attr_vals = val.attr_vals.split(',')
            val.attr_vals.forEach(attr_value => {
            this.editGoodForm.attrs.push({
                attr_id: val.attr_id,
                attr_value: attr_value
            })
            })
        })
        //   []
        const {data: res_only} = await this.$http.get(`categories/${good_cat_id}/attributes`, { params:  {sel: "only"} })
        if(res_only.meta.status !== 200) return this.$message.error('')
        this.onlyAttrs = res_only.data
        // 
        res.data.pics.forEach(img => {
            this.imgList.push({
                name: '',
                url: img.pics_sma_url
            })
        })
      },
    //   
      async getGoodClass () {
          const {data: res} = await this.$http.get('categories')
          if(res.meta.status !== 200) return this.$message.error('')
          this.goodClass = res.data
          this.goodClass.forEach(val0 => {
              var val0_children = []
              val0.children.forEach(val1 => {
                  if(val1.children){
                  var val1_children = []
                    val1.children.forEach(val2 => {
                        val1_children.push({
                            value: val2.cat_id,
                            label: val2.cat_name
                        })
                    })
                    val0_children.push({
                      value: val1.cat_id,
                      label: val1.cat_name,
                      children: val1_children
                    })
                  }
                  else{
                    val0_children.push({
                      value: val1.cat_id,
                      label: val1.cat_name,
                    })
                  }
              })
              this.goodClassOptions.push({
                  value: val0.cat_id,
                  label: val0.cat_name,
                  children: val0_children
              })
          })
      },
    //   
      choseVal (attr_id, attr_value, isCheck) {
          if(isCheck){
              this.editGoodForm.attrs.push({
                  attr_id: attr_id,
                  attr_value: attr_value
              })
          }else{
              var index = this.editGoodForm.attrs.findIndex(val => {
                  return val.attr_value = attr_value
              })
              this.editGoodForm.attrs.splice(index, 1)
          }
      },
    //    Tab
      changeTab (activeName, oldActiveName) {
          if(activeName != 0){
            var result = true
            this.$refs['editGoodFormRef'].validate(valid => {
                if(valid) this.active = parseInt(activeName)
                result = valid
            })
            return result
          }
      },
    //    => 
      async onEditorBlur (val) {
          if(this.active != 4) return
          const result = await this.$confirm(', ?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
          }).catch(err => err)
          if( result == 'confirm'){
              this.editGoodForm.pics = this.imgList
              this.onlyAttrs.forEach(attr => {
                this.editGoodForm.attrs.push({
                    attr_id: attr.attr_id,
                    attr_value: attr.attr_vals
                })
              })
            //   
              const {data: res} = await this.$http.put(`goods/${this.id}`, this.editGoodForm);
            //    => 
              if(res.meta.status !== 200)   return this.$message.error('!');
            //    => 
              this.active = 5
              this.$message.success('');
              this.$router.push('/goods'); 
          }
      }
  }
}
</script>

<style lang='css' scoped>
.el-tag {
    width: 100%;
    text-align: center;
}
.el-steps {
    margin: auto;
    margin-top: 10px;
    margin-bottom: 10px;
    width: 90%;
}
.manyAttr {
    margin: 10px
}
.el-checkbox {
    margin: 10px;
}
.ql-editor{
    height:300px !important;
}
</style>



