<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <!--  -->
            <el-table :data="ordersList" style="width: 100%" border stripe>
                <el-table-column label="#" type="index"></el-table-column>
                <el-table-column label="" prop="order_number"></el-table-column>
                <el-table-column label="" prop="order_price" width="80px"></el-table-column>
                <el-table-column label=""  width="80px">
                    <template slot-scope="scope">
                        <el-tag v-if="scope.row.order_pay == 1" type="success" size="mini"></el-tag>
                        <el-tag v-else type="danger" size="mini"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="is_send"  width="80px"></el-table-column>
                <el-table-column label="" prop="create_time" width="150px">
                    <template slot-scope="scope">
                        {{ scope.row.add_time | formatDate }}
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[10, 20, 50, 100]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
import moment from 'moment'
export default {
  name: 'Goods',
  data () {
      return {
        //   
        queryInfo: {
            query: '',
            pagenum: 1,
            pagesize: 10
        },
        // 
        ordersList: [],
        // 
        total: 0,
        infoDialogVisible: false,
        infoForm: {},
        activities: [{
                        content: '',
                        timestamp: '2018-04-15'
                     }, {
                        content: '',
                        timestamp: '2018-04-13'
                     }, {
                        content: '',
                        timestamp: '2018-04-11'
                     }]
      }
  },
  created () {
      this.getOrdersList()
  },
  filters: {
      formatDate: function (value) {
        return moment(value).format('YYYY-MM-DD HH:mm:ss')
      }
  },
  methods: {
    //   
      async getOrdersList () {
          const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
          if(res.meta.status !== 200) return this.$message.error('')
          this.ordersList = res.data.goods
          this.total = res.data.total
      },
    //    pagesize
      handleSizeChange(val) {
        this.queryInfo.pagesize = val
        this.getOrdersList()
      },
    //    
      handleCurrentChange(val) {
        this.queryInfo.pagenum = val
        this.getOrdersList()
      },
  }
}
</script>

<style lang='css' scoped>
</style>



