<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <!--  -->
            <el-button class="btn" @click="showAddDialog" type="primary"></el-button>
                        <!--  -->
            <el-dialog
                title=""
                :visible.sync="addDialogVisible"
                width="40%"
                @close="addHandleClose('addFormRef')">
                <el-form ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="80px">
                    <el-form-item label="" prop="cat_name">
                        <el-input v-model="addForm.cat_name"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="cat_name">
                        <el-cascader
                            v-model="currentClass"
                            :options="goodClassOptions">
                        </el-cascader>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="addDialogVisible = false"> </el-button>
                    <el-button type="primary" @click="addHandle('addFormRef')"> </el-button>
                </span>
            </el-dialog>
            <!--  -->
            <zk-table
                ref="table"
                :data="goodCatList"
                :columns="columns"
                :stripe="props.stripe"
                :border="props.border"
                :show-header="props.showHeader"
                :show-summary="props.showSummary"
                :show-row-hover="props.showRowHover"
                :show-index="props.showIndex"
                :tree-type="props.treeType"
                :is-fold="props.isFold"
                :expand-type="props.expandType"
                :selection-type="props.selectionType"
                sum-text="sum"
                index-text="#">
                <template slot="catDeleted" slot-scope="scope">
                    <i v-if="scope.row.cat_deleted" class="el-icon-error"></i>
                    <i v-else class="el-icon-success"></i>
                </template>
                <template slot="catLevel" slot-scope="scope">
                    <el-tag v-if="scope.row.cat_level == 0" size="mini"></el-tag>
                    <el-tag v-else-if="scope.row.cat_level == 1" type="success" size="mini"></el-tag>
                    <el-tag v-else type="warning" size="mini"></el-tag>
                </template>
                <template slot="option" slot-scope="scope">
                    <!--  -->
                    <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                        <el-button @click="showEditDialog(scope.row.cat_id)" type="primary" icon="el-icon-edit" size="mini"></el-button>
                    </el-tooltip>
                    <!--  -->
                    <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                        <el-button @click="showDeleteDialog(scope.row.cat_id)" type="danger" icon="el-icon-delete" size="mini"></el-button>
                    </el-tooltip>
                </template>
            </zk-table>
            <!--  -->
            <el-dialog
                title=""
                :visible.sync="editDialogVisible"
                width="40%"
                @close="editHandleClose('editFormRef')">
                <el-form ref="editFormRef" :model="editForm" :rules="editFormRules" label-width="80px">
                    <el-form-item label="" prop="cat_name">
                        <el-input v-model="editForm.cat_name"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="editDialogVisible = false"> </el-button>
                    <el-button type="primary" @click="editHandle('editFormRef')"> </el-button>
                </span>
            </el-dialog>
            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[10, 20, 50, 100]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
export default {
  name: 'Rights',
  data () {
      return {
        //   
        queryInfo: {
            query: '',
            pagenum: 1,
            pagesize: 10
        },
        // 
        goodCatList: [],
        // []
        total: 0,
        props: {stripe: false,
                border: true,
                showHeader: true,
                showSummary: false,
                showRowHover: true,
                showIndex: true,
                treeType: true,
                isFold: true,
                expandType: false,
                selectionType: false},
        columns:  [{label: '',
                    prop: 'cat_name'},
                   {label: '',
                    prop: 'cat_deleted',
                    type: 'template',
                    template: 'catDeleted'},
                   {label: '',
                    prop: 'cat_level',
                    type: 'template',
                    template: 'catLevel'},
                   {label: '',
                    type: 'template',
                    template: 'option'}],
        defaultProps:  {children: 'children',
                        label: 'label'},
        editDialogVisible: false,
        editForm: {
            cat_id: Number,
            cat_name: ''
        },
        editFormRules: {
            cat_name: [
                { required: true, message: '', trigger: 'blur' }
            ],
        },
        addDialogVisible: false,
        addForm: {
            cat_pid: 0,
            cat_name: '',
            cat_level: 0
        },
        addFormRules: {
            cat_name: [
                { required: true, message: '', trigger: 'blur' }
            ],
        },
        currentClass: '',
        goodClassOptions: [],
      }
  },
  created () {
      this.getGoodCatList()
      this.getGoodClass()
  },
  methods: {
    // 
    async getGoodCatList () {
        const { data: res } = await this.$http.get('categories', { params: this.queryInfo })
        if(res.meta.status !== 200) return this.$message.error('')
        this.goodCatList = res.data.result
        this.total = res.data.total
        console.log(this.goodCatList)
    },
    //   
    async getGoodClass () {
        const {data: res} = await this.$http.get('categories')
        if(res.meta.status !== 200) return this.$message.error('')
        this.goodClass = res.data
        this.goodClass.forEach(val0 => {
            var val0_children = []
            val0.children.forEach(val1 => {
                val0_children.push({
                    value: val1.cat_id,
                    label: val1.cat_name,
                })
            })
            this.goodClassOptions.push({
                value: val0.cat_id,
                label: val0.cat_name,
                children: val0_children
            })
        })
    },
    //    pagesize
    handleSizeChange(val) {
        this.queryInfo.pagesize = val
        this.getGoodCatList()
    },
    //    
    handleCurrentChange(val) {
        this.queryInfo.pagenum = val
        this.getGoodCatList()
    },
    // 
    async showEditDialog (id) {
        const {data: res} = await this.$http.get(`categories/${id}`)
        if(res.meta.status !== 200) return this.$message.error("")
        this.editForm.cat_id = res.data.cat_id
        this.editForm.cat_name = res.data.cat_name
        this.editDialogVisible = true
    },
    // 
    editHandle (formName) {
        this.$refs[formName].validate(async valid => {
            if(!valid) return;
            const {data: res} = await this.$http.put(`categories/${this.editForm.cat_id}`, {cat_name: this.editForm.cat_name})
            if(res.meta.status !== 200) return this.$message.error("")
            this.editDialogVisible = false
            this.getGoodCatList()
        })
    },
    // 
    editHandleClose (formName) {
        this.editDialogVisible = false
        this.$refs[formName].resetFields();
    },
    //   
    async showDeleteDialog (id) {
        const result = await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)
        if( result == 'confirm'){
            const {data: res} = await this.$http.delete(`categories/${id}`)
            if(res.meta.status !== 200)   return this.$message.error('!');
            this.$message.success('');
            this.getGoodCatList()
        }
    },
    // 
    showAddDialog () {
        this.addDialogVisible = true
    },
    // 
    addHandle (formName) {
        this.$refs[formName].validate(async valid => {
            if(!valid) return;
            console.log(this.addForm)
            console.log(this.currentClass)
            if(this.currentClass){
                this.addForm.cat_pid = this.currentClass[this.currentClass.length-1]
                this.addForm.cat_level = this.currentClass.length
            }
            const {data: res} = await this.$http.post('categories', this.addForm)
            console.log(res)
            if(res.meta.status !== 201) return this.$message.error("")
            this.$message.success("")
            this.getGoodCatList()
            this.addDialogVisible = false
        })
    },
    // 
    addHandleClose (formName) {
        this.editDialogVisible = false
        this.$refs[formName].resetFields();
    }
  }
}
</script>

<style lang='css' scoped>
.btn {
    margin-bottom: 20px;
}
.el-icon-success {
    color: #4F94CD;
}
.el-icon-error {
    color: grey;
}
</style>



