<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <!--  -->
            <el-row :gutter="20">
                <!--  -->
                <el-col :span="8">
                    <el-input placeholder="" v-model="queryInfo.query" clearable @clear="clearSearchContent" class="input-with-select">
                        <el-button @click="search" slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
                <!--  -->
                <el-col :span="4">
                    <el-button @click="showAddDialog" type="primary"></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="goodList" style="width: 100%" border stripe>
                <el-table-column label="#" type="index"></el-table-column>
                <el-table-column label="" prop="goods_name" width="400px"></el-table-column>
                <el-table-column label="()" prop="goods_price"></el-table-column>
                <el-table-column label="" prop="goods_weight"></el-table-column>
                <el-table-column label="" prop="add_time">
                    <template slot-scope="scope">
                        {{ scope.row.add_time | formatDate }}
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <!--  -->
                        <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                            <el-button @click="showEditDialog(scope.row.goods_id)" type="primary" icon="el-icon-edit" size="mini"></el-button>
                        </el-tooltip>
                        <!--  -->
                        <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                            <el-button @click="showDeleteDialog(scope.row.goods_id)" type="danger" icon="el-icon-delete" size="mini"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[10, 20, 50, 100]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
import moment from 'moment'
export default {
  name: 'Goods',
  data () {
      return {
        //   
        queryInfo: {
            query: '',
            pagenum: 1,
            pagesize: 10
        },
        // 
        goodList: [],
        // 
        total: 0,
      }
  },
  created () {
      this.getGoodsList()
  },
  filters: {
      formatDate: function (value) {
        return moment(value).format('YYYY-MM-DD HH:mm:ss')
      }
  },
  methods: {
      async getGoodsList () {
          const { data: res } = await this.$http.get('goods', { params: this.queryInfo })
          if(res.meta.status !== 200) return this.$message.error('')
          this.goodList = res.data.goods
          this.total = res.data.total
      },
    //    pagesize
      handleSizeChange(val) {
        this.queryInfo.pagesize = val
        this.getGoodsList()
      },
    //    
      handleCurrentChange(val) {
        this.queryInfo.pagenum = val
        this.getGoodsList()
      },
    //   
      search () {
          this.getGoodsList()
      },
    //   
      clearSearchContent () {
          this.getGoodsList()
      },
    //   
      showAddDialog () {
          this.$router.push('/addgood'); 
      },
    //   
      async showEditDialog (id) {
          this.$router.push(`/editgoods/${id}`); 
      },
    //   
      async showDeleteDialog (id) {
          console.log(id)
        const result = await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)
        if( result == 'confirm'){
            const {data: res} = await this.$http.delete(`goods/${id}`);
            if(res.meta.status !== 200)   return this.$message.error('!');
            this.$message.success('');
            this.getGoodsList()
        }
      }
  }
}
</script>

<style lang='css' scoped>
</style>



